<template>
  <div class="home">
    <!-- 内容 -->
    <div class="wrap-top">
      <van-nav-bar :border="false" left-arrow @click-left="onClickLeft" />
      <div class="tip-title" v-if="recordStatus === 1 || recordStatus === 3">请面向屏幕开始录制视频</div>
      <div class="tip-title" v-if="recordStatus === 2">请面向屏幕 {{ titles }}</div>
      <div class="tip-title" v-if="recordStatus === 4">正在检测中...</div>

      <!-- 人像部分 -->
      <cw-video :showDefaultImg="!isOpenVideo" :loading="loading" ref="cwVideoRef">
        <template #imgSlot>
          <div class="video-wrap" id="video-box" v-show="isOpenVideo">
            <video id="videoPlay" playinline autoplay></video>
          </div>
        </template>
      </cw-video>
    </div>
    <div class="wrap-bottom">
      <!-- 提示信息 -->
      <TipInfo v-if="isShowInfo" />
      <!-- 功能按钮 -->
      <div class="op-btn" v-if="isShowBtn">
        <CwButton @click="openCamera" :loading="btnLoading">开始录制视频</CwButton>
      </div>
    </div>

    <!-- 弹出面板 -->
    <CwNativeCameraPopup v-model="showNativeCamera" :titles="titles" @nativeRecord="nativeRecord" @cancelClick="nativeCancelClick" />
    <CwRsultPopup v-model="showResult" @reRecord="reRecord" @cancelClick="cancelClick" />
  </div>
</template>

<script>
import CwButton from '@/components/cw-button/index.vue'
import CwVideo from '@/components/cw-video/index.vue'
import CwNativeCameraPopup from '@/components/cw-native-camera-popup/index.vue'
import CwRsultPopup from '@/components/cw-result-popup/index.vue'
import axios from 'axios'
import TipInfo from './components/tip-info.vue'
const URL = window.config.uploadUrl
const ACTION_NUM = window.config.actionNum || 2 // 动作个数
export default {
  components: {
    CwButton,
    CwVideo,
    CwNativeCameraPopup,
    CwRsultPopup,
    TipInfo
  },
  data() {
    return {
      showNativeCamera: false,
      showResult: false,
      //  cwVideoRef: false,
      videoInstance: null,
      isSupport: true,
      btnLoading: false,
      recordStatus: 1,
      activeActions: [],
      isUploadFinish: false,
      actionsArr: [
        { title: '眨眨眼', action: '1' },
        { title: '张张嘴', action: '2' },
        { title: '摇摇头', action: '5' }
      ]
    }
  },
  computed: {
    isOpenVideo() {
      return this.isSupport && this.recordStatus === 2
    },
    isShowInfo() {
      return this.recordStatus === 1 || this.recordStatus === 3
    },
    isShowBtn() {
      return this.recordStatus === 1 || this.recordStatus === 3
    },
    loading() {
      return this.recordStatus === 4 && !this.isUploadFinish
    },
    titles() {
      const titlesArr = this.activeActions.map(ele => ele.title)
      return titlesArr.join('、')
    },
    actions() {
      const actionsArr = this.activeActions.map(ele => ele.action)
      return actionsArr.join(',')
    }
  },
  mounted() {
    this.initFs()
    this.initVideo()
  },
  methods: {
    // 初始化动作
    initFs() {
      if (ACTION_NUM === 1) {
        const num = Math.floor(Math.random() * (this.actionsArr.length - 1) + 1)
        this.activeActions = this.actionsArr.filter((ele, index) => index === num - 1)
      } else if (ACTION_NUM === 2) {
        const num = Math.floor(Math.random() * (this.actionsArr.length - 1) + 1)
        this.activeActions = this.actionsArr.filter((ele, index) => index !== num - 1)
      } else {
        this.activeActions = [...this.actionsArr]
      }
    },

    // 打开cameara
    openCamera() {
      const self = this
      if (self.isSupport) {
        self.btnLoading = true
        // 支持页面内录制
        // 打开摄像头
        self.videoInstance.openCamera({
          onError() {
            console.warn('不支持')
            self.showNativeCamera = true
            self.isSupport = false
            self.recordStatus = 3
            self.btnLoading = false
          },
          onSuccess() {
            console.warn('支持')
            self.recordStatus = 2
            self.startCollect()
            self.btnLoading = false
          }
        })
      } else {
        // 不支持页面内录制
        self.showNativeCamera = true
      }
    },

    // 开始录制
    startCollect() {
      const recordTime = (window.config.recordTime || 5000) / 1000
      let isFirst = true
      const self = this
      self.videoInstance.startCollect({
        time: recordTime, // 可不设置, 不设置需要手动控制结束
        timeCallback(clock) {
          if (isFirst) {
            isFirst = false
            self.$refs.cwVideoRef.start(recordTime)
          }
          console.warn(clock)
          if (clock <= 0) {
            self.recordStatus = 4
          }
        }
      })
    },

    // 初始化摄像头
    initVideo() {
      this.recordStatus = 1
      const self = this
      // 录制视频初始化
      this.videoInstance = new CWMediaCollector({
        webmWasmPath: './static/webm-wasm.wasm', // 依赖文件
        webmWorkerPath: './static/webm-worker.js', // 依赖文件webm-worker.js的本地路径，不传参数则默认调用服务端获取文件
        el: document.getElementById('video-box'), // document.getElementById('video-box'), // 视频显示位置, 视频窗口根据el大小适配
        height: 480,
        width: 480,
        autoOpenNativeCamera: false, // 失败的时候不自动打开摄像头
        mode: 1, // mode, // 模式【1：兼容性优先；2：体验优先；3：原生相机录制视频】
        maxFileSize: 10, // 10, // 非必传，限制最大文件(M)
        frameRate: 10,
        isFrontCamera: true,
        mimeType: 'video/webm;codecs=h264',
        onInitSuccess(res) {
          if (res.code === 20002) {
            self.isSupport = false
            // 弹窗触发
          } else {
            self.isSupport = true
          }
        },
        onSuccess: function (res) {
          // 录制结束回调函数
          console.log('recordingEnd==', res)
          if (res.code === 20000) {
            console.log('录制结束了————————')
            if (res.data) {
              self.postBlob(res.data)
              console.warn('录取的数据', res.data)
              self.recordStatus = 4
              self.showNativeCamera = false
            } else {
              console.warn('录制的数据为0')
              self.showResult = true
            }
          } else {
            // 录制未成功
            self.showResult = true
          }
        },
        onError: function (res) {
          console.log('🚀 ~ html ~ initVideo ~ res', res, res.code, '--', res.message)
          console.log(res.message)
          // 录制过程中出错
          self.showResult = true
        }
      })
    },
    // 存储结果
    setResult(result) {
      localStorage.setItem('result', JSON.stringify(result))
    },
    // 视频传输
    postBlob(blob) {
      const self = this
      CWMediaCollector.blobToBase64(blob, function (base64Data) {
        console.warn('录制的文件', base64Data)
        console.warn('文件大小Byte', base64Data.length)
        self.checkFace(base64Data, function (res) {
          console.log(res)
          if (res.isAlive && res.image) {
            self.setResult({
              result: true,
              size: base64Data.length,
              image: 'data:image/jpeg;base64,' + res.image,
              res
            })
            self.$router.push({
              path: '/result'
            })
          } else if (!res.isAlive && res.result === 0) {
            self.showResult = true
          } else if (!res.isAlive && res.result !== 0) {
            self.showResult = true
          }
        })
      })
    },
    // 调用接口检测face
    checkFace(videoData, callback) {
      // 调用检测接口
      const data = {
        ...(window.config.uploadParameter || {}),
        video: videoData,
        action: this.actions
      }
      this.isUploadFinish = false
      axios
        .post(URL, data)
        .then(res => {
          if (res.status === 200) {
            callback(res.data)
          } else {
            console.warn('失败status !== 200:', res.status)
          }
        })
        .catch(err => {
          console.warn('请求出错:', err)
        })
        .finally(() => {
          this.isUploadFinish = true
        })
    },
    // 再次录制视频
    reRecord() {
      console.warn('重新录制功能')
      this.showResult = false
      this.$refs.cwVideoRef.init() // 初始化进度
      this.initFs()
      this.initVideo()
    },
    // 原生视频录制
    nativeRecord() {
      this.videoInstance && this.videoInstance.openNativeCamera()
    },
    // 点击了返回功能
    onClickLeft() {
      console.warn('点击了返回键盘')
      const url = window.config.returnUrl
      location.href = url
      // 回调地址
    },

    // 失败的时候退出
    cancelClick() {
      console.warn('点击了失败的退出功能')
      // 退出回调
      const url = window.config.failedUrl
      location.href = url
    },
    // 原生相机的退出功能
    nativeCancelClick() {
      const url = window.config.returnUrl
      location.href = url
    }
  }
}
</script>

<style lang="less" scoped>
.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  .op-btn {
    padding: 0 28px;
  }
  .tip-title {
    font-size: 36px;
    color: #333742;
    text-align: center;
    line-height: 36px;
    font-weight: 400;
    padding-top: 120px;
    padding-bottom: 60px;
  }
  .wrap-bottom {
    padding-bottom: 120px;
    padding-top: 60px;
  }
}
</style>
